/* Week7.css */
.App {
    transition: background-color 0.5s ease;
  }
  
  .dark-mode {
    background-color: #333; /* Dark mode background color */
    color: #fff; /* Dark mode text color */
  }
  
  .light-mode {
    background-color: #f9f9f9; /* Light mode background color */
    color: #333; /* Light mode text color */
  }
  
  .navbar {
    background-color: #00aa4f7f; /* Background color of the navigation bar */
    padding: 10px;
  }
  
  .navbar-nav {
    display: flex;
    justify-content: flex-end; /* Align items to the right */
    list-style-type: none; /* Remove default list style */
    padding-right: 10px; /* Add padding to the right side */
  }
  
  .navbar-nav .nav-link {
    padding: 0.5rem 1rem; /* Adjust padding as needed */
    margin-right: 10px; /* Add margin between nav items */
    color: #d2af00; /* Adjust text color as needed */
    text-decoration: none; /* Remove underline */
  }
  
  .navbar-nav .nav-link:hover {
    color: #ec2e10; /* Change color on hover */
  }
  
  .navbar-nav .nav-link.highlight {
    color: #c7432c; /* Change color for highlighted items */
  }
  
  .nav-item {
    border: 1px solid #000; /* Add border style */
    border-radius: 5px; /* Optional: Add border-radius for rounded corners */
    padding: 5px 10px; /* Optional: Adjust padding for better appearance */
  }
  
  .nav-item:hover {
    background-color: #f0f0f0; /* Optional: Change background color on hover */
  }
  
  .highlight {
    text-decoration: none; /* Optional: Remove underline from links */
    color: #000; /* Optional: Set text color */
  }
  
  .navbar-brand {
    display: flex; /* Use flexbox for alignment */
    align-items: center; /* Align items vertically */
    margin-bottom: 20px;
  }
  
  .navbar-brand img {
    border-radius: 50%; /* Make the logo round */
  }
  
  .dark-mode-toggle {
    position: fixed;
    top: 20px;
    right: 20px;
    font-size: 24px;
    cursor: pointer;
  }
 
/* Search toggle */
.search-toggle {
  cursor: pointer;
  margin-left: 1rem;
}




  /* Search box */
.navbar-search {
  position: relative;
  display: flex;
  align-items: center;
}

.search-input {
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  border: 1px solid #ccc;
  margin-right: 1rem;
}

.search-button {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 0.5rem;
  padding: 1rem;
  cursor: pointer;
}

.search-icon {
  font-size: 1rem;
}


.navbar-dark-mode-toggle {
  cursor: pointer;
  position: absolute;
  top: 10px; /* Adjust as needed */
  right: 10px; /* Adjust as needed */
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
}
