/* SignUpPage.css */

.signup-form-container {
    max-width: 400px;
    margin: 50px auto;
    padding: 35px;
    border: 25px solid #ccc;
    border-radius: 150px;
    background-color: #0ae9dae8;
    box-shadow: 10 10 10px rgba(106, 1, 1, 0.1);
  }
  
  .signup-form-container h2 {
    text-align: center;
    margin-bottom: 30px;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  .form-group input {
    width: 90%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ec0909;
    border-radius: 50px;
  }
  
  button[type="submit"] {
    width: 100%;
    padding: 12px 0;
    font-size: 18px;
    border-radius: 50px;
